                            "key": "date",
                            "parent": null,
                            "shown": true
                        },
                        {
                            "name": "Tipo de envío",
                            "key": "type",
                            "parent": null,
                            "shown": true
                        },
                        {
                            "key": 'amountSent',
                            "name": 'Monto enviado',
                            "parent": null,
                            "shown": true,
                        },
                        {
                            "key": 'status',
                            "parent": null,
                            "shown": true,
                            "name": 'Estado'
                        } 
            
                ]
            }]
    }

    get getFields(){
        return {
                "name": 'Vista original',
                "headerPrimary": [
                ],
                "headerSecondary":  [
                        {
                            "name": "Fecha",
                            "key": "date",
                            "parent": null,
                            "shown": true
                        },
                        {
                            "name": "Tipo de envío",
                            "key": "type",
                            "parent": null,
                            "shown": true
                        },
                        {
